<template>
  <div class="users">
    <HelloWorld msg="Users CRUD" />
    <form class="myModal" v-if="updatingModal" @submit.prevent="submitEditUser()">
      <h3>Enter new data:</h3>
      <div class="d-flex mb-3">
        <div class="mr-2">
          <span>Name:</span>
          <input type="text" v-model="newName" />
        </div>
        <div>
          <span>Location:</span>
          <input type="text" v-model="newLocation" />
        </div>
      </div>
      <p class="error" v-if="errorLength">Name and location must be at list 2 letters length!</p>
      <input type="submit" class="btn btn-success" value="Save" />
      <div class="closeModal" @click="updatingModal = false">x</div>
    </form>
    <div class="wrap d-flex flex-column">
      <form class="d-flex flex-column mb-3" @submit.prevent="submitAddUser()">
        <h3>Add user</h3>
        <div class="d-flex mb-2">
          <label for="name" class="mr-2">Name:</label>
          <input type="text" id="name" v-model="newUserName" required />
        </div>
        <div class="d-flex mb-2">
          <label for="location" class="mr-2">Location:</label>
          <input type="text" id="location" v-model="newUserLocation" required />
        </div>
        <div class="d-flex mb-2">
          <label for="currency" class="mr-2">Curency:</label>
          <input type="number" min="0" v-model="newUserCurency" required />
        </div>
        <input type="submit" value="Add" />
      </form>
      <div class="search d-flex flex-column text-left">
        <h3>Search</h3>
        <div class="d-flex">
          <label for="name" class="mr-2">Name:</label>
          <input type="text" id="name" v-model="name" />
        </div>
        <div class="d-flex">
          <label for="location" class="mr-2">Location:</label>
          <input type="text" id="location" v-model="location" />
        </div>
        <label for="currency">Curency:</label>
        <div class="currency d-flex mb-3">
          <div class="w-50 mr-3">
            <span>From:</span>
            <input type="number" class="mb-0" id="currencyFrom" v-model="currencyFrom" />
          </div>
          <div class="w-50">
            <span>To:</span>
            <input type="number" class="mb-0" id="currencyTo" v-model="currencyTo" />
          </div>
        </div>
      </div>
      <table class="users-list mr-3 mb-5">
        <thead>
          <tr>
            <td></td>
            <td>
              <div class="d-flex justify-content-center">
                <span class="mr-2">Name</span>
                <div class="arrowToggle" :class="{reverse: sortNameDesc}" @click="sortName()"></div>
              </div>
            </td>
            <td>
              <div class="d-flex justify-content-center">
                <span class="mr-2">Location</span>
                <div
                  class="arrowToggle"
                  :class="{reverse: sortLocationDesc}"
                  @click="sortLocation()"
                ></div>
              </div>
            </td>
            <td>Currency</td>
            <td></td>
            <td></td>
            <td></td>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(user, id) in usersFiltered" :key="++id">
            <td>
              <p>{{ id }}</p>
            </td>
            <td>
              <p>{{ user.name }}</p>
            </td>
            <td>
              <p>{{ user.location }}</p>
            </td>
            <td>
              <p>{{ user.currency }}</p>
            </td>
            <td class="edit" @click="editUser(id)" title="edit"></td>
            <td class="eye" @click="viewUser(user._id)" title="view details"></td>
            <td class="delete" @click="deleteUser(id)" title="delete"></td>
          </tr>
          <tr>
            <td colspan="7" class="text-center">currency sum: {{currencySum}}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.wrap {
  max-width: 600px;
  margin: auto;
}
.arrowToggle {
  width: 20px;
  height: 20px;
  background: url("../assets/arrow.svg") no-repeat center;
  background-size: contain;
  cursor: pointer;
  transition: 0.2s;
  &.reverse {
    transform: rotateX(180deg);
  }
}
.users-list {
  border: 1px solid #dadada;
  border-radius: 10px;
  input {
    border: none;
  }
  p {
    margin-bottom: 0;
  }
  tr {
    margin-bottom: 10px;
    td {
      padding: 5px;
      border-right: 1px solid #dadada;
      border-bottom: 1px solid #dadada;
    }
    td:last-child {
      border-right: none;
    }
  }
  .edit {
    background: url("../assets/edit.png") no-repeat center;
    background-size: 80%;
    min-width: 35px;
    max-width: 35px;
    cursor: pointer;
  }
  .eye {
    background: url("../assets/eye.png") no-repeat center;
    background-size: 80%;
    min-width: 35px;
    max-width: 35px;
    cursor: pointer;
  }
  .delete {
    background: url("../assets/delete.png") no-repeat center;
    background-size: 65%;
    min-width: 35px;
    max-width: 35px;
    cursor: pointer;
  }
}
.search {
  input {
    margin-bottom: 15px;
  }
}
.myModal {
  position: fixed;
  transition: 0.3s;
  height: 180px;
  padding: 20px;
  border-radius: 20px;
  background: #dadada;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  .closeModal {
    position: absolute;
    top: 9px;
    right: 16px;
    font-size: 20px;
    cursor: pointer;
  }
}
.error {
  color: red;
}
input {
  width: 100%;
}
</style>

<script>
// @ is an alias to /src
import HelloWorld from "@/components/HelloWorld.vue";

export default {
  name: "users",
  components: {
    HelloWorld
  },
  data: () => {
    return {
      users: [],
      name: "",
      location: "",
      currencyFrom: 0,
      currencyTo: 0,
      isMenuOpen: false,
      maxCurrency: null,
      sortNameDesc: true,
      sortLocationDesc: true,
      newName: "",
      newLocation: "",
      updatingModal: false,
      editedUserId: null,
      errorLength: false,
      newUserName: "",
      newUserLocation: "",
      newUserCurency: null,
      newUserName: "",
    };
  },
  methods: {
    submitAddUser() {
      this.axios
        .post(`${this.$store.state.domen}/users`, {
          name: this.newUserName,
          location: this.newUserLocation,
          currency: this.newUserCurency
        })
        .then(response => this.users.push(response.data))
        .catch(error => console.log(error));
    },
    viewUser(id) {
      this.$store.commit("setUserId", id);
      window.location = `/#/users/${id}`;
    },
    editUser(id) {
      this.updatingModal = true;
      this.editedUserId = id;
      [this.newName, this.newLocation] = [
        this.usersFiltered[--id].name,
        this.usersFiltered[id].location
      ];
    },
    deleteUser(id) {
      let userOfList = this.users[--id];
      this.users.splice(id, 1);
      this.axios
        .delete(`${this.$store.state.domen}/users/${userOfList._id}`)
        // .then(response => console.log(response))
        .catch(error => console.log(error));
    },
    submitEditUser() {
      let userChosen = this.usersFiltered[--this.editedUserId];
      this.errorLength = false;
      if (this.newName.length < 2 || this.newLocation.length < 2) {
        return (this.errorLength = true);
      }
      if (
        this.newName !== userChosen.name ||
        this.newLocation !== userChosen.location
      ) {
        this.updatingModal = false;
        this.axios
          .put(`${this.$store.state.domen}/users`, {
            id: userChosen.id,
            name: this.newName,
            location: this.newLocation
          })
          .then(response => {
            for (let user of this.usersFiltered) {
              if (user.id === response.data.id) {
                [user.name, user.location] = [
                  response.data.name,
                  response.data.location
                ];
              }
            }
          })
          .catch(error => console.log(error));
      }
    },
    sortName() {
      if (this.updatingModal) return false;
      if (this.sortNameDesc) {
        this.users.sort((a, b) => b.name.localeCompare(a.name));
        this.sortNameDesc = false;
      } else {
        this.users.sort((a, b) => a.name.localeCompare(b.name));
        this.sortNameDesc = true;
      }
    },
    sortLocation() {
      if (this.updatingModal) return false;
      if (this.sortLocationDesc) {
        this.users.sort((a, b) => b.location.localeCompare(a.location));
        this.sortLocationDesc = false;
      } else {
        this.users.sort((a, b) => a.location.localeCompare(b.location));
        this.sortLocationDesc = true;
      }
    }
  },
  created() {
    fetch(`${this.$store.state.domen}/users`)
      .then(users => {
        return users.json();
      })
      .then(users => {
        this.maxCurrency = users[0].currency;
        users.forEach(user => {
          this.users.push(user);
          if (user.currency > this.maxCurrency)
            this.maxCurrency = user.currency;
        });
        this.currencyTo = this.maxCurrency;
      });
  },
  computed: {
    currencySum() {
      let sum = 0;
      this.usersFiltered.forEach(user => (sum += user.currency));
      return sum;
    },
    usersFiltered() {
      return this.users.filter(user => {
        return (
          user.name.toLowerCase().includes(this.name.toLowerCase()) &&
          user.location.toLowerCase().includes(this.location.toLowerCase()) &&
          user.currency > this.currencyFrom &&
          user.currency < this.currencyTo
        );
      });
    }
  }
};
</script>
