<template>
  <div>
    <h2>Name: {{user.name}}</h2>
    <p>Location: {{user.location}}</p>
    <p>Currency: {{user.currency}}</p>
    <p v-if="user.id">ID: {{user.id}}</p>
    <p v-else>MongoID: {{user._id}}</p>
  </div>
</template>

<script>
export default {
  name: "User",
  data: () => {
    return {
      user: {}
    };
  },
  created() {
    let id = this.$store.state.id || window.location.href.split("users/")[1];
    fetch(`http://localhost:3000/users/${id}`)
      .then(user => {
        return user.json();
      })
      .then(user => (this.user = user[0]));
  }
};
</script>